<template>
  <!-- Contact -->
  <section class="contact">
    <div class="container">
      <h2>Contact</h2>
      <span class="contact-txt">{{ typeValue }}</span>
      <div class="contact-wrap">
        <img src="../../images/kakao_qr.jpg" alt="오픈카톡">
      </div>
    </div>
  </section>
  <!-- // Contact -->
</template>

<script>
  export default {
    name: "index",

    data() {
      return {
        typeValue: '',
        typeStatus: false,
        typeArray: ['"Thank you for reading it."'],
        typingSpeed: 200,
        erasingSpeed: 100,
        newTextDelay: 1500,
        typeArrayIndex: 0,
        charIndex: 0
      }
    },

    methods: {
      typeText() {
        if (this.charIndex < this.typeArray[this.typeArrayIndex].length) {
          if (!this.typeStatus)
            this.typeStatus = true;

          this.typeValue += this.typeArray[this.typeArrayIndex].charAt(this.charIndex);
          this.charIndex += 1;

          setTimeout(this.typeText, this.typingSpeed);
        } else {
          this.typeStatus = false;
          setTimeout(this.eraseText, this.newTextDelay);
        }
      },
      eraseText() {
        if (this.charIndex > 0) {
          if (!this.typeStatus)
            this.typeStatus = true;

          this.typeValue = this.typeArray[this.typeArrayIndex].substring(0, this.charIndex - 1);
          this.charIndex -= 1;
          setTimeout(this.eraseText, this.erasingSpeed);
        } else {
          this.typeStatus = false;
          this.typeArrayIndex += 1;
          if (this.typeArrayIndex >= this.typeArray.length)
            this.typeArrayIndex = 0;

          setTimeout(this.typeText, this.typingSpeed + 1000);
        }
      }
    },

    created() {
      setTimeout(this.typeText, this.newTextDelay + 200);
    }
  }
</script>

<style scoped>
  /* Work */
  .contact {
    width: 100%;
  }

  .contact .container {
    text-align: center;
  }

  .contact .container h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 3.2rem;
    margin-bottom: 3rem;
  }

  .contact-txt {
    display: inline-block;
    height: 4rem;
    font-family: 'Playfair Display', sans-serif;
    font-size: 3.2rem;
    letter-spacing: 0.3rem;
  }

  .contact-wrap {
    background: url('../../images/full-moon.png') no-repeat center / cover;
    padding: 5rem 0;
    margin-top: 5rem;
  }

  .contact-wrap img {
    width: 30rem;
    height: 30rem;
  }


  @media (max-width: 1400px) {
    .contact {
      margin: 5rem 0;
    }

    .contact-txt {
      margin-bottom: 5rem;
    }
  }

  @media (max-width: 768px) {
    .contact {
      width: 90%;
    }

    .contact-txt {
      font-size: 1.6rem;
      margin-bottom: 2rem;
    }

    .container {
      margin: 2rem auto;
    }

    .contact-wrap {
      padding: 1rem 0;
      margin-top: 0;
    }

    .contact-wrap img {
      width: 20rem;
      height: 20rem;
    }
  }
  /* //Work */
</style>