<template>
  <!-- Work -->
  <section class="work">
    <div class="container">
      <h2>Work</h2>
      <span>“우리 인생은 우리들이 노력한만큼 가치가 있다.”</span>
      <ul class="work-btn-list">
        <li>
          <router-link @click="isPubFocus = true, isDesignFocus = false" to="/work/pub"
            class="work-btn" :class="{work_btn_focus: isPubFocus == true}">Projects</router-link>
        </li>
        <li>
          <router-link @click="isPubFocus = false, isDesignFocus = true" to="/work/design"
            class="work-btn" :class="{work_btn_focus: isDesignFocus == true}">Design</router-link>
        </li>
      </ul>
      <div class="work-cont">
        <div class="work-wrap">
          <transition name="pubAni">
            <router-view v-if="loading == false"></router-view>
          </transition>
        </div>
      </div>
    </div>
  </section>
  <!-- // Work -->
</template>

<script>
  export default {
    name: 'index',

    data() {
      return {
        loading: true,
        isPubFocus: true,
        isDesignFocus: false,
      }
    },

    mounted() {
      this.$nextTick(function () {
        this.loading = false;
      })
    }
  };
</script>

<style>
  /* Work */
  .work {
    width: 100%;
  }

  .work .container {
    text-align: center;
  }

  .work .container h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 3.2rem;
  }

  .work .container>span {
    display: block;
    font-size: 1.8rem;
  }

  .work-btn-list {
    display: flex;
    justify-content: center;
    margin: 5rem 0;
  }

  .work-btn-list li {
    margin: 0 1rem;
  }

  .work-btn {
    background: #FEFEEB;
    border: none;
    padding: 1rem 1.5rem;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.4rem;
    color: #222C40;
    font-weight: 700;
    letter-spacing: 0.25rem;
    transition: color 0.2s, background 0.2s;
  }

  .work_btn_focus {
    background: #222C40;
    color: #FEFEEB;
    box-shadow: 0 0 0 0.2rem #FEFEEB inset;    
  }

  .work-cont {
    position: relative;
    width: 100%;
  }

  .work-wrap {
    margin: 0 5rem;
  }

  .pubAni-enter-active {
    animation: fade 1s ease-in-out;
  }

  .pubAni-leave-from {
    display: none;
  }

  @keyframes fade {
    0% {
      opacity: 0;
      transform: scale(0.9);
    }

    30% {
      transform: scale(0.8);
    }

    100% {
      opacity: 1;
      transform: scale(1);
    }
  }

  .work-list {
    width: 100%;
    border: 0.5rem solid #FEFEEB;
    overflow: hidden;
  }

  .work-bg {
    min-width: 100%;
    max-width: 36rem;
    height: 32rem;
    margin: 0 auto;
  }

  .work-dark-bg {
    position: relative;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.5s ease;
  }

  .work-dark-bg p {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    font-size: 2.4rem;
    color: #FEFEEB;
    font-weight: 700;
    letter-spacing: 0.15rem;
  }

  .work-dark-bg p>span {
    display: block;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.5;
  }

  .work-dark-bg p span:first-child {
    margin-top: 2rem;
  }

  .work-dark-bg p a {
    display: inline-block;
    width: 11rem;
    height: 4rem;
    background: #FEFEEB;
    border: none;
    margin: 1rem 0.5rem 0.5rem 0.5rem;
    transform: scale(0);
    font-family: 'Montserrat', sans-serif;
    font-size: 1.6rem;
    color: #222C40;
    font-weight: 400;
    line-height: 2.5;
    letter-spacing: 0.15rem;
    text-align: center;
    transition: transform 0.001s, color 0.3s, background 0.3s;
  }

  .work-list:hover .work-dark-bg {
    opacity: 1;
  }

  .work-list:hover .work-dark-bg p a {
    transform: scale(1);
  }

  .work-dark-bg p a:hover {
    background: #222C40;
    color: #FEFEEB;
    box-shadow: 0 0 0 0.2rem #FEFEEB inset;
  }

  @media (max-width: 1400px) {
    .work {
      margin: 5rem 0;
    }

    .work .container>span {
      margin-bottom: 3rem;
    }
  }

  @media (max-width: 768px) {
    .work {
      width: 90%;
    }

    .work .container>span {
      font-size: 1.6rem;
    }
  }

  @media (max-width: 425px) {
    .work-btn-list {
      flex-direction: column;
      align-items: center;
      margin: 1rem 0;
    }

    .work-btn-list>li {
      margin-bottom: 2rem;
    }

    .work-wrap {
      margin: 0 2rem;
    }

    .work-cont {
      margin-bottom: 0;
    }

    .work-dark-bg p {
      font-size: 2rem;
    }

    .work-dark-bg p>span {
      font-size: 1.4rem;
    }

    .work-dark-bg p a {
      height: 3.5rem;
      font-size: 1.4rem;
      letter-spacing: 0.1rem;
    }
  }
  /* //Work */
</style>