<template>
  <!-- Design -->
  <swiper v-bind="swiperOptions" :modules="modules" @swiper="onSwiper" @mouseenter="() => this.swiper.autoplay.stop()"
    @mouseleave="() => this.swiper.autoplay.start()">
    <swiper-slide>
      <div class="work-list">
        <div class="work-bg design01">
          <div class="work-dark-bg">
            <p>
              Portfolio
              <span>&#35;Photoshop</span>
              <a href="#" target="_black">Work</a>
              <a href="https://github.com/HalfMoonKim/HalfMoonKim.github.io" target="_blank"><i
                  class="fab fa-github"></i>
                GitHub</a>
            </p>
          </div>
        </div>
      </div>
    </swiper-slide>
    <swiper-slide>
      <div class="work-list">
        <div class="work-bg design02">
          <div class="work-dark-bg">
            <p>
              Banksy 공식 사이트
              <span>&#35;Photoshop</span>
              <a href="http://www.banksy.co.uk/" target="_black">Origin</a>
              <a href="#" target="_black">Work</a>
              <a href="#" target="_blank"><i class="fab fa-github"></i> GitHub</a>
            </p>
          </div>
        </div>
      </div>
    </swiper-slide>
  </swiper>
  <!-- // Design -->
</template>

<script>
  import SwiperCore, {
    Autoplay,
    Pagination
  } from 'swiper';
  import {
    Swiper,
    SwiperSlide
  } from 'swiper/vue';
  import 'swiper/swiper-bundle.min.css';
  SwiperCore.use([Autoplay, Pagination]);

  export default {
    name: 'Publishing',

    components: {
      Swiper,
      SwiperSlide,
    },

    setup() {
      return {
        swiperOptions: {
          loop: true,
          loopAdditionalSlides: 1,
          speed: 1200,
          slidesPerView: 1,
          slidesPerGroup: 1,
          spaceBetween: 10,
          centeredSlides: true,
          autoplay: {
            "delay": 3000,
            "disableOnInteraction": true,
          },
          pagination: {
            "clickable": true
          },

          breakpoints: {
            768: {
              slidesPerView: 2,
            },
            1024: {
              slidesPerView: 2.5
            }
          },
        },

        modules: [Pagination, Autoplay],
      };
    },

    data() {
      return {
        swiper: null,
      }
    },
    methods: {
      onSwiper(swiper) {
        this.swiper = swiper;
      },
    },
  }
</script>

<style scoped>
  .design01 {
    background: url('../images/portfolio.png') no-repeat center / cover;
  }

  .design02 {
    background: url('../images/banksy_pc.jpg') no-repeat center / cover;
  }
</style>