<script>
  import SwiperCore, {
    Autoplay,
    Pagination
  } from 'swiper';
  import {
    Swiper,
    SwiperSlide
  } from 'swiper/vue';
  import 'swiper/swiper-bundle.min.css';
  SwiperCore.use([Autoplay, Pagination]);

  export default {
    name: 'Publishing',

    components: {
      Swiper,
      SwiperSlide,
    },

    setup() {
      return {
        swiperOptions: {
          loop: true,
          loopAdditionalSlides: 1,
          speed: 1200,
          slidesPerView: 1,
          slidesPerGroup: 1,
          spaceBetween: 10,
          centeredSlides: true,
          autoplay: {
            "delay": 3000,
            "disableOnInteraction": true,
          },
          pagination: {
            "clickable": true
          },

          breakpoints: {
            768: {
              slidesPerView: 2,
            },
            1024: {
              slidesPerView: 2.5
            }
          },
        },

        modules: [Pagination, Autoplay],
      };
    },

    data() {
      return {
        swiper: null,
      }
    },
    methods: {
      onSwiper(swiper) {
        this.swiper = swiper;
      },
    },
  }
</script>

