<script>
  import SwiperCore, {
    Autoplay,
    Pagination,
  } from 'swiper';
  import {
    Swiper,
    SwiperSlide
  } from 'swiper/vue';
  import 'swiper/swiper-bundle.min.css';
  SwiperCore.use([Pagination, Autoplay]);
  import Skills from '@/components/Skills.vue'
  import whoList from '@/assets/who'
  import faqList from '@/assets/faq'

  export default {
    name: "index",

    components: {
      Skills,
      Swiper,
      SwiperSlide,
    },

    setup() {
      return {
        swiperOptions: {
          loop: true,
          loopAdditionalSlides: 1,
          speed: 1200,
          slidesPerView: 1,
          slidesPerGroup: 1,
          spaceBetween: 50,
          autoplay: {
            "delay": 3000,
            "disableOnInteraction": true,
          },
          pagination: {
            "clickable": true
          },
        },

        modules: [Autoplay],
      };
    },

    data() {
      return {
        stateFaq: false,
        isAboutFocus: true,
        isFaqFocus: false,
        whoList: whoList,
        faqList: faqList,
        active: 0,
        stateSkills: false,
        swiper: null,
      }
    },

    methods: {
      onSwiper(swiper) {
        this.swiper = swiper;
      },

      activeClass: function ([i]) {
        if (this.active == i) {
          this.active = null;
        } else {
          this.active = i;
        }
      }
    },
  }
</script>

